
.base {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 100;
  display: block;
}
.load-once {
  background-color: #28282d;
  background-image: url('../assets/images/avatar.png');
  background-position: center 35%;
  background-size: 120px;
  background-repeat: no-repeat;
  opacity: 1;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.load-once.remove {
  opacity: 0;
}
.load-once .text {
  width: 100%;
  height: 100%;
  color: #fff;
  text-align: center;
  font-size: 28px;
  line-height: 50%;
  padding-top: 120%;
}



